import React from "react";
import { setTimeout, clearTimeout } from "timers";

class Typewriter extends React.Component {
    state = {
        text: ""
    }

    startTyping() {
        if (!this.props.text) {
            return;
        }

        if (this.props.text.lenght >= this.textIndex) {
            return;
        }

        this.timeout = setTimeout(function () {
            this.setState({
                text: `${this.state.text}${this.props.text.substring(this.textIndex, 1)}`
            });

            console.log(this.textIndex);

            this.textIndex++;
            this.startTyping();
        }.bind(this), 500)
    }

    stopTyping() {
        clearTimeout(this.timeout);
    }

    componentDidMount() {
        this.textIndex = 0;
        this.startTyping()
    }

    componentWillUnmount() {
        this.stopTyping();
    }

    render() {
        return (
            <React.Fragment>
                {this.state.text}
            </React.Fragment>
        )
    }
}

export default Typewriter;